
.auth {
  background-color: #F9F9F9;
  padding-top: 50px;
  width: 100%;
  height: 800px;
}
.auth .auth-login {
    margin: 0 auto;
    width: 260px;
    padding: 50px;
    margin-top: 80px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
