}
.auth .auth-login-form form input {
          margin: 0;
          border: 1px solid #E6E6E6;
          height: 45px;
          font-size: 14px;
          text-indent: 16px;
          border-radius: 1px;
          background-color: #F9F9F9;
}
.auth .auth-login-form form button {
